@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    scroll-padding-top: 0rem;
    min-height: 100%;
    position: relative;
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    background-color: v.$fondo;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    margin: 0;
    margin-bottom: 40px;
    

    &.fijar-body {
        overflow: hidden;
    }

    &.body-scroll {
        padding-top: 8.6rem;
    }
}

p {
    font-size: 2rem;
    line-height: 1.5;
}

.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
    color: v.$grisclaro;

    &:hover {
        cursor: pointer;
    }
}

.a {
    text-decoration: none;
    color: v.$grisclaro;

    &:hover {
        cursor: pointer;
    }
}

h1,
h2,
h3 {
    margin: 0 0 5rem 0;
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 3.6rem;
}

h3 {
    font-size: 3rem;
}

img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}