.tituloOrderHistory {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    color: #e8ddbd;
    height: 50px;
    width: 400px;
    padding-top: 7px;
    margin-top: 20px;
    background-color: #9d1d36;
    margin: 0px auto;
    
  }
  
  .contenedorGral {
    margin: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0;
    margin-bottom: 40px;
  }
  
  .contenedorBooks {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2rem;
    align-items: center;
    margin: 0 auto;
    flex-wrap: wrap;
    flex-direction: row;
  }
  
  .buttonPerfil {
    margin-top: 10px;
    margin-left: 7px;
    padding: 4px;
    border-radius: 10px;
    background: #0D0F10;
    font-size: 17px;
    border-color: #0D0F10;
    color: #F6AA1C;
  }
  
  .buttonPerfil:hover {
    text-decoration: underline;
  }
  
  .btnBook {
    background-color: #9d1d36;
    color: #e8ddbd;
    margin: 20px 2.5px 0px 0px;
    padding: 7px 15px;
    margin-bottom: 2%;
    margin-left: 1%;
  }